import axiosClient from "./axiosClient";

const HistoryAPI = {
  // Ly danh sch lch s n hng, c th truyn query (param) nu cn
  getHistoryAPI: (query) => {
    const url = `/orders`;
    return axiosClient.get(url, {
      params: query, // Thm query vo params nu c
      withCredentials: true,
    });
  },

  // Ly chi tit n hng theo id
  getDetail: (id) => {
    const url = `/orders/${id}`;
    return axiosClient.get(url, {
      withCredentials: true,
    });
  },

  // Ly tt c cc n hng (c th s dng khi khng cn query)
  getAll: () => {
    const url = `/orders`;
    return axiosClient.get(url, {
      withCredentials: true,
    });
  },
};

export default HistoryAPI;
